.assist {
	padding: 45px 0;
	@include r(930) {
		padding-top: 30px; }
	&__header {
		font-size: 34px;
		line-height: 42px;
		margin-bottom: 50px;
		@include r(930) {
			line-height: 30px;
			margin-bottom: 30px; } }
	&__content {
		display: flex;
		justify-content: space-between;
		max-width: 900px;
		align-items: center;
		@include r(730) {
			flex-wrap: wrap;
			justify-content: space-around; } }
	&__item {
		text-align: center;
		padding: 0 20px;
		@include r(730) {
			margin-bottom: 15px; }
		p {
			@include helreg;
			color: $grey-light;
			font-size: 13px;
			line-height: 20px; } }
	&__title {
		margin-bottom: 18px;
		@include r(730) {
			margin-bottom: 5px; } }
	&__sep {} }

